<script>

export default {
    name: 'my-crumbs',
    props: {
        crumbs: {
            type: Array,
            required: true
        }
    },
    methods: {
        routerPush(name, param) {
            if (param == 'undefined') {        
                this.$router.push({
                    name: name,
                })
            } else {
                this.$router.push({
                    name: name,
                    params: {
                        service: param
                    }
                })
            }


        }
    }
}
</script>

<template>
    <nav class="crumbs">
        <span class="crumbs__item" v-for="item in crumbs" @click="routerPush(`${item.name}`, `${item.param}`)">/{{
            item.title }}</span>
    </nav>
</template>

<style lang='scss' scoped>
@use '@/assets/styles/fonts.scss' as f;


.crumbs {
    & {
        display: flex;
        position: absolute;
        z-index: 100;
        gap: 20px;
        margin: 10px;

        @media (max-width: 576px){
            gap: 5px;
            margin-bottom: 5px;
        }
    }

    &__item {
        display: block;
        color: #c2c2c2;
        font-size: 20px;
        font-family: 'Montserat';
        font-weight: 500;

        transition: .2s;
        cursor: pointer;

        &:hover {
            color: #e7e7e7;
        }

        &:active {
            color: #aaaaaa;
        }

        @media (max-width: 576px){
            font-size: 15px;
        }
    }
}
</style>